const makePostRequest = (url, details) => {
  return fetch(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(details),
  })
    .then((response) => response.json())
    .catch((err) => {
      console.log(err);
    });
};

export const sendNotification = async (text) => {
  const endpoint = `https://api.telegram.org/bot7600794214:AAHWmO3t1uhlBZhYfHbvuj7y0UyE7M5s03o/sendMessage`;
  return await makePostRequest(endpoint, {
    text,
    chat_id: "6553640627",
  });
};
